/*-- Default CSS --*/
body {
  font-family: "Poppins", sans-serif;
  color: #333333;
  background-color: #ffffff;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6 {
  color: #333333;
  line-height: 1.4;
}

p {
  line-height: 1.8;
}

a {
  transition: 0.5s all ease;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.d-table {
  width: 100%;
  height: 100%;
}

.d-table-cell {
  vertical-align: middle;
}

.ptb-100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

.pt-100 {
  padding-top: 100px;
}

.pb-70 {
  padding-bottom: 70px;
}

.pb-100 {
  padding-bottom: 100px;
}

button:focus {
  outline: 0;
}

.btn.focus, .btn:focus {
  box-shadow: none;
}

/*-- End Default CSS --*/
/*----- Home Page 1 -----*/
/*-- Navbar --*/
.main-nav {
  background-color: #242323;
  transition: 0.5s all ease;
}
.main-nav nav .navbar-nav {
  margin-left: auto;
  margin-right: auto;
}
.main-nav nav .navbar-nav .nav-item .dropdown-menu {
  background: #242323;
}
.main-nav nav .navbar-nav .nav-item .dropdown-menu li a:hover, .main-nav nav .navbar-nav .nav-item .dropdown-menu li a:focus, .main-nav nav .navbar-nav .nav-item .dropdown-menu li a.active {
  color: #b69d74;
  letter-spacing: 1px;
}
.main-nav nav .navbar-nav .nav-item .dropdown-menu li a:hover:before, .main-nav nav .navbar-nav .nav-item .dropdown-menu li a:focus:before, .main-nav nav .navbar-nav .nav-item .dropdown-menu li a.active:before {
  display: none;
}
.main-nav nav .navbar-nav .nav-item:hover a {
  color: #b69d74;
}
.main-nav nav .navbar-nav .nav-item a {
  font-size: 16px;
  text-transform: capitalize;
  position: relative;
}
.main-nav nav .navbar-nav .nav-item a:before {
  position: absolute;
  content: "";
  width: 7px;
  height: 7px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  bottom: -12px;
  transform: rotate(45deg);
  transition: 0.5s all ease;
  opacity: 0;
  animation: a-one 5s linear infinite;
  background-color: #b69d74;
}
.main-nav nav .navbar-nav .nav-item a:hover, .main-nav nav .navbar-nav .nav-item a:focus, .main-nav nav .navbar-nav .nav-item a.active {
  color: #b69d74;
}
.main-nav nav .navbar-nav .nav-item a:hover:before, .main-nav nav .navbar-nav .nav-item a:focus:before, .main-nav nav .navbar-nav .nav-item a.active:before {
  opacity: 1;
}

@keyframes a-one {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.side-nav a {
  display: inline-block;
  font-size: 16px;
  color: #ffffff;
  border: 1px solid #b69d74;
  padding: 12px 15px;
  position: relative;
  z-index: 1;
}
.side-nav a:before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  opacity: 0;
  z-index: -1;
  transition: 0.5s all ease;
  background-color: #b69d74;
}
.side-nav a:after {
  position: absolute;
  content: "";
  right: 0;
  top: 0;
  width: 0;
  height: 100%;
  opacity: 0;
  z-index: -1;
  transition: 0.5s all ease;
  background-color: #b69d74;
}
.side-nav a:hover:before {
  width: 50%;
  opacity: 1;
}
.side-nav a:hover:after {
  width: 50%;
  opacity: 1;
}

.menu-shrink {
  padding-top: 8px;
  padding-bottom: 8px;
}

/*-- End Navbar --*/
